<template>
	<view class="user_my">
		<view class="bg_img">
		</view>
		<view class="user">
			<view class="user_avatar">
				<img v-show="login" :src="avaURL" alt="">
				<img v-show="!login" src="../../static/user/default.png" alt="">
			</view>
			<span>{{getname}}</span>
			<button @click="login_user">{{login==true?"退出登录":"登录"}}</button>
		</view>

	</view>
</template>

<script>
	export default {
		// token: '',"https://p4.music.126.net/SUeqMM8HOIpHv9Nhl9qt9w==/109951165647004069.jpg"
		// avatarUrl: '',
		// userName: '',
		computed: {
			getname() {
				if (this.login == true) return this.name
				else return "未登录"
			}
		},
		data() {
			return {
				login: false,
				name: '',
				avaURL: '',
			};
		},
		onShow() {
			this.login = this.$store.state.login,
				this.name = this.$store.state.nickName,
				this.avaURL = this.$store.state.avatarUrl
		},
		methods: {
			login_user() {
				if (this.login == false) {
					uni.navigateTo({
						url: '/pages/login/login',
						// animationDuration: 200
					})
				} else {
					this.$store.commit('logout')
					uni.showToast({
						title: "退出登录成功"
					})
				}
			}
		}
	}
</script>

<style lang="scss" scoped>
	.user_my {
		display: flex;
		flex-direction: column;
		align-items: center;
		background-color: #ebebeb;
		height: 100vh;

		.bg_img {
			height: 50vw;
			width: 100vw;
			background: url(https://img2.baidu.com/it/u=1521904622,3503251141&fm=253&fmt=auto&app=138&f=JPEG?w=500&h=184);
		}

		.user {
			display: flex;
			flex-direction: column;
			align-items: center;
			width: 90vw;
			height: 70vw;
			background-color: #fff;
			border-radius: 3vw;
			box-shadow: 0 1vw 1vw 0.5vw #ebebeb;
			transform: translate(0, -3vw);

			.user_avatar {
				transform: translate(0, -15vw);
				width: 30vw;
				height: 30vw;
				border-radius: 50%;
				overflow: hidden;

				img {
					width: 30vw;
					height: 30vw;
				}


			}

			span {
				padding: 2vw;
				font-weight: 600;
				font-size: 40rpx;
			}

			button {
				width: 50vw;
				height: 10vw;
				display: flex;
				justify-content: center;
				align-items: center;
				margin-top: 5vw;
				background-color: #e8e8e8;

				&:hover {
					background-color: #d6d6d6;
				}
			}
		}
	}
</style>
